import request, { cacheRequest } from "./request";

const PRE_URL = "/Module/GasStation/GasStationHandler.ashx?action=";

export const getPayInfo = () => request(PRE_URL + "GetPayInfo");

// 
export const checkMember = (cardId) =>
  request(PRE_URL + "CheckSupportModel", { data: { cardId } });

// ,
export const getMemberList = (cardId) =>
  request(PRE_URL + "GetMembersPagedByCardId", { data: { cardId } });

// 
// cardId, goodsItemGuid
export const getMemberInfo = (data) =>
  request(PRE_URL + "GetMemberInfoByCardId", { data });

export const getGasDecrease = (data) =>
  request(PRE_URL + "GetValueGasDecreaseItem", { data });

// 
export const getVerifyCode = (memberGuid) =>
  request(PRE_URL + "CreateDYMemberPassword", { data: { memberGuid } });
export const payOrder = (consumeEntity) =>
  request(PRE_URL + "GasCheckOutConsume", { data: { consumeEntity } });

let order_times = 0;
export const getOrderList = () =>
  request(
    PRE_URL + "GetNoPayList" + "&times=" + order_times++,
    { data: {} },
    { loading: false, toast: false }
  );

export const getOrderNumberStatus = (orderNumber) =>
  request(
    PRE_URL + "GetOrderNumberStatus",
    { data: { orderNumber } },
    { loading: false }
  );
