const cashTacticType = {
  CashNormal: 0, // 
  CashPerFullSubduction: 1, // 
  CashRandomSubduction: 2, // 
  CashSubduction: 3, // 
  CashRandomSubductionPercent: 4, // 
  CashPerLitreDiscount: 6, // 
};

class CashTactic {
  acceptCash(activityGuid, money, unit) {
    return 0;
  }

  display() {
    return "";
  }
}

//
class CashNormal extends CashTactic {
  constructor(body) {
    super();
  }

  acceptCash(activityGuid, money) {
    return money;
  }

  display() {
    return "";
  }
}

//
class CashPerFullSubduction extends CashTactic {
  constructor(body) {
    body = body.CashPerFullSubduction;
    super();
    this.fullNumber = body.FullNumber;
    this.subNumber = body.SubNumber;
    this.maxSubNumber = body.MaxSubNumber;
  }

  acceptCash(activityGuid, money) {
    let count = Math.floor(money / this.fullNumber);
    var cash = count * this.subNumber;
    if (this.maxSubNumber > 0 && cash > this.maxSubNumber) {
      return Math.fixed(Math.subtract(money, this.maxSubNumber), 2);
    }
    return Math.fixed(Math.subtract(money, cash), 2);
  }

  display() {
    if (this.maxSubNumber > 0) {
      return `${this.fullNumber}${this.subNumber},${this.maxSubNumber}`;
    } else {
      return `${this.fullNumber}${this.subNumber}`;
    }
  }
}

//
class CashRandomSubduction extends CashTactic {
  constructor(body) {
    super();
    body = body.CashRandomSubduction;
    this.maxNumberm = body.MaxNumber;
    this.minNumber = body.MinNumber;
    this.fullNumber = body.FullNumber;
  }

  //
  static genarateRandomValue(activityGuid, money) {
    //TODO:openId
    var day = new Date()
      .toISOString()
      .substring(0, 10)
      .replace("-", "")
      .replace("-", "");
    var key = "radom_" + day + "_" + activityGuid + "_" + money;
    var ran;
    if (localStorage.getItem(key)) {
      ran = localStorage.getItem(key) * 1;
    } else {
      ran = Math.random();
      localStorage.setItem(key, ran);
    }
    return Math.fixed(ran, 2);
  }

  acceptCash(activityGuid, money) {
    if (money < this.fullNumber) {
      return money;
    } else {
      let random = CashRandomSubduction.genarateRandomValue(
        activityGuid,
        money
      );
      let discountRandom = Math.fixed(
        Math.mult(random, Math.subtract(this.maxNumberm, this.minNumber)),
        2
      );
      let discountCash = Math.add(this.minNumber, discountRandom); //
      return Math.fixed(Math.subtract(money, discountCash), 2);
    }
  }

  display() {
    return `${this.fullNumber}${this.maxNumberm}`;
  }
}

//
class CashRandomSubductionPercent extends CashTactic {
  constructor(body) {
    super();
    body = body.CashRandomSubductionPercent;
    this.sections = [];
    this.fullNumber = body.FullNumber;
    for (var s of body.Sections.Section) {
      this.sections.push({
        start: s.Start,
        end: s.End,
        percent: s.Percent,
      });
    }
  }

  //
  generateRandomValue(activityGuid, money) {
    //TODO:
    var day = new Date()
      .toISOString()
      .substring(0, 10)
      .replace("-", "")
      .replace("-", "");
    var key = "radom_" + day + "_" + activityGuid + "_" + money;
    var key2 = "radom_" + day + "_" + activityGuid + "_2_" + money;
    var ran;
    if (localStorage.getItem(key)) {
      ran = +localStorage.getItem(key);
    } else {
      ran = Math.fixed(Math.random(), 2);
      localStorage.setItem(key, ran);
    }
    // let ran = Math.random();//0-1
    let end = 0;
    let value = 0;
    var sections = this.sections;
    for (var e of sections) {
      end = Math.add(end, Math.divide(e.percent, 100));
      if (ran < end) {
        var sectionRandomValue;
        if (localStorage.getItem(key2)) {
          sectionRandomValue = +localStorage.getItem(key2);
        } else {
          sectionRandomValue = Math.fixed(Math.random(), 2);
          localStorage.setItem(key2, sectionRandomValue);
        }
        // let sectionRandomValue = Math.random(); //0-1
        let sectionDiscountRandom = Math.mult(
          sectionRandomValue,
          Math.subtract(e.end, e.start)
        );
        let sectionDiscountCash = Math.add(e.start, sectionDiscountRandom);
        value = Math.divide(
          Math.subtract(sectionDiscountCash, sections[0].start),
          Math.subtract(sections[sections.length - 1].end, sections[0].start)
        );
        //0~1
        break;
      }
    }
    return value;
  }

  acceptCash(activityGuid, money) {
    if (money < this.fullNumber) {
      return money;
    }
    var randomValue = this.generateRandomValue(activityGuid, money);
    var discountRandomPart = Math.fixed(
      Math.mult(
        randomValue,
        Math.subtract(
          this.sections[this.sections.length - 1].end,
          this.sections[0].start
        )
      ),
      2
    );
    var discountCash = Math.add(this.sections[0].start, discountRandomPart); //
    return Math.fixed(Math.subtract(money, discountCash), 2);
  }

  display() {
    let lastIndex = this.sections.length - 1;
    return `${this.fullNumber}${this.sections[0].start}~${this.sections[lastIndex].end}`;
  }
}

// 
class CashSubduction extends CashTactic {
  constructor(body) {
    super();
    body = body.CashSubduction;
    var level = body.LevelSections.LevelSection;
    this.levelSections = [];
    if (level.length === undefined) {
      this.levelSections.push({
        fullNumber: level.FullNumber,
        subNumber: level.SubNumber,
      });
    } else {
      for (var s of level) {
        if (s) {
          this.levelSections.push({
            fullNumber: s.FullNumber,
            subNumber: s.SubNumber,
          });
        }
      }
    }
  }

  acceptCash(activityGuid, money) {
    var levelSection = this.levelSections
      .sort(function (a, b) {
        return b.fullNumber - a.fullNumber;
      })
      .find((t) => t.fullNumber <= money);
    if (!levelSection) {
      return money;
    }
    var discountCash = levelSection.subNumber; //
    return Math.subtract(money, discountCash);
  }

  display() {
    var str = "";
    for (var LevelSection of this.levelSections) {
      str += `,${LevelSection.fullNumber}${LevelSection.subNumber}`;
    }
    return str.length > 0 ? str.substring(1) : str;
  }
}

// 

/*
* {
    "CashPerLitreDiscount": {
        "LevelSections": {
            "LevelSection": [
                {
                    "FullNumber": "1",
                    "SubNumber": "1"
                },
                {
                    "FullNumber": "2",
                    "SubNumber": "1"
                },
                {
                    "FullNumber": "3",
                    "SubNumber": "2"
                },
                {
                    "FullNumber": "4",
                    "SubNumber": "2"
                },
                {
                    "FullNumber": "5",
                    "SubNumber": "2"
                }
            ]
        },
        "type": "3"
    }
}
* */
class CashPerLitreDiscount extends CashTactic {
  constructor(body) {
    super();
    body = body.CashPerLitreDiscount;
    const level = body.LevelSections.LevelSection.filter(Boolean);
    this.levelSections = [];
    if (level.length === undefined) {
      this.levelSections.push({
        fullNumber: level.FullNumber,
        subNumber: level.SubNumber,
      });
    } else {
      this.levelSections = level.map((it) => ({
        fullNumber: it.FullNumber,
        subNumber: it.SubNumber,
      }));
    }
  }

  acceptCash(activityGuid, money, unit) {
    var levelSection = this.levelSections
      .sort(function (a, b) {
        return b.fullNumber - a.fullNumber;
      })
      .find((t) => t.fullNumber <= money);
    if (!levelSection) {
      console.log(
        "[activity]--[CashPerLitreDiscount]",
        this.levelSections.length ? "" : ""
      );
      return money;
    }
    var discountCash = Math.fixed(Math.mult(unit, levelSection.subNumber), 2); //
    console.log(
      `${levelSection.fullNumber}${levelSection.subNumber}, ${unit}${discountCash}`
    );
    return Math.subtract(money, discountCash);
  }

  display() {
    return this.levelSections.reduce(
      (p, x) => p + `${x.fullNumber}${x.subNumber},`,
      ""
    );
  }
}

export default class CashContext {
  constructor(body, type) {
    body = JSON.parse(body);
    switch (type) {
      case cashTacticType.CashNormal:
        this.cashSuper = new CashNormal(body);
        break;
      case cashTacticType.CashPerFullSubduction:
        this.cashSuper = new CashPerFullSubduction(body);
        break;
      case cashTacticType.CashRandomSubduction:
        this.cashSuper = new CashRandomSubduction(body);
        break;
      case cashTacticType.CashRandomSubductionPercent:
        this.cashSuper = new CashRandomSubductionPercent(body);
        break;
      case cashTacticType.CashSubduction:
        this.cashSuper = new CashSubduction(body);
        break;
      case cashTacticType.CashPerLitreDiscount:
        this.cashSuper = new CashPerLitreDiscount(body);
        break;
      default:
    }
  }

  static create(body, type) {
    return new CashContext(body, type);
  }

  //  
  getResult(activityGuid, money, unit) {
    return this.cashSuper.acceptCash(activityGuid, money, unit);
  }

  display() {
    return this.cashSuper.display();
  }
}
