const parent = window.parent === window ? null : window.parent;
const PRODUCTION = process.env.NODE_ENV === "production";

const dftOption = {
  response: true, // ,,
};

function noticeParent(eventName, payload, options) {
  options = { ...dftOption, ...options };
  return new Promise((resolve) => {
    if (!parent || !parent.EVENT_BUS)
      return resolve({
        success: false,
        message: "",
      });
    const event = parent.EVENT_BUS;
    event.emit(eventName, payload);
    if (!options.response) return resolve({ success: true });
    event.clear(eventName + "_complete");
    event.on(eventName + "_complete", resolve);
  });
}

// ,.
export const addValue = (payload) => {
  return new Promise((resolve) => {
    if (process.env.NODE_ENV === "development") {
      setTimeout(() => {
        resolve({ success: true });
      }, 500);
      return;
    }
    if (!parent || !parent.EVENT_BUS) throw "";
    const event = parent.EVENT_BUS;

    // 
    event.clear("GasStationAddValueComplete");
    event.emit("GasStationAddValue", payload);
    event.on("GasStationAddValueComplete", (result) => {
      event.clear("GasStationAddValueComplete");
      resolve(result);
    });
  });
};

// ,
// ,
export const onParentNotice = (callback) => {
  if (process.env.NODE_ENV === "development") {
    return setTimeout(callback, 500);
  }
  if (!parent || !parent.EVENT_BUS) throw "";
  const event = parent.EVENT_BUS;
  event.on("shouldRefreshOilOrder", callback);
  return () => {
    event.clear("shouldRefreshOilOrder");
  };
};

// ()
export const getPayCode = (data) => {
  if (!PRODUCTION)
    return Promise.resolve({
      success: true,
      data: { paymentCode: "1234123412341234" },
    });
  return noticeParent("gas_checkout_on_request_pay_code", data);
};

// 
export const cancelPayCode = () => {
  if (!PRODUCTION) return Promise.resolve({ success: true });
  return noticeParent("gas_checkout_on_cancel_pay_code", null, {
    response: false,
  });
};

export const paymentSucceed = (data) => {
  if (!PRODUCTION) return Promise.resolve({ success: true });
  return noticeParent("gas_checkout_on_payment_succeed", data, {
    response: false,
  });
};

export const paymentFailed = (data) => {
  if (!PRODUCTION) return Promise.resolve({ success: true });
  return noticeParent("gas_checkout_on_payment_failed", data, {
    response: false,
  });
};
