import * as API from "@/api";
import * as utils from "@/utils";
import { getUsedPaytype, getThirdPaytype } from "@/utils/paytype-manager";
import CalculatorContext from "@/utils/calculator/index";
import "../../utils/math";
import ca from "element-ui/src/locale/lang/ca";

/*
 * .
 * 1. getters,,.
 * 2. getters.
 * 3. ,dispatch.
 * 4. dispatch,,CalculatorContext
 * 5. CalculatorContext,,await
 * */

const dftCheckMap = {
  discount: false,
  activity: false,
  value: false,
  coupon: false,
  point: false,
  motherCard: false,
};

let SET_DISCOUNT_CHECK_MAP_MANUAL = false;

export default {
  namespaced: true,
  state: {
    orderMoney: "",
    // 
    params: null,
    // ()
    gunList: [],
    // guid
    goodsItemGuid: "",
    // 
    memberCardId: "",
    // [][]
    memberData: null,
    // [][]
    discountParams: null,
    // [][]()
    coupons: [],
    // 
    currentConflict: [],
    // 
    discountMap: {},
    // 
    discountCheckMap: {
      ...dftCheckMap,
    },
    //guid
    oilGunGuid: "",
    oilIndexGuid: "",
    // 
    pickedCoupons: [],

    // ,
    orderList: [],

    // ,
    noPayOrder: null,
    // 
    staffList: [],

    // 
    pickedStaff: [],
  },
  mutations: {
    SET_ORDER_MONEY: (state, money) => (state.orderMoney = money),
    SET_GUN_LIST: (state, list) => (state.gunList = list),
    SET_MEMBER_DATA: (state, data) => (state.memberData = data),
    SET_PARAMS: (state, params) => (state.params = params),
    SET_DISCOUNT_PARAMS: (state, data) => (state.discountParams = data),
    SET_COUPONS: (state, data) => (state.coupons = data),
    SET_DISCOUNT_MAP: (state, map) => (state.discountMap = map),
    SET_GOODS_ITEM_GUID: (state, guid) => (state.goodsItemGuid = guid),
    SET_MEMBER_CARD_ID: (state, guid) => (state.memberCardId = guid),
    SET_CURRENT_CONFLICT: (state, keys) => (state.currentConflict = keys),
    SET_OIL_GUN_GUID: (state, guid) => (state.oilGunGuid = guid),
    SET_OIL_INDEX_GUID: (state, guid) => (state.oilIndexGuid = guid),
    SET_DISCOUNT_CHECK_MAP: (state, map) => (state.discountCheckMap = map),
    SET_PICKED_COUPONS: (state, list) => (state.pickedCoupons = list),
    SET_ORDER_LIST: (state, list) => (state.orderList = list),
    SET_NO_PAY_ORDER: (state, order) => (state.noPayOrder = order),
    SET_STAFF_LIST: (state, list) =>
      (state.staffList = list.map((it) => ({
        ...it,
        key: it.staffGuid,
        text: `${it.staffName} (${it.staffAccount})`,
      }))),
    SET_PICKED_STAFF: (state, list) => (state.pickedStaff = list),
    RESET_DISCOUNT_CHECK_MAP: (state) =>
      (state.discountCheckMap = { ...dftCheckMap }),
  },
  actions: {
    // 
    async getPageData({ commit }) {
      const result = await API.getPayInfo();
      if (!result.success) return;
      commit("SET_GUN_LIST", result.data.guns);
      commit("SET_STAFF_LIST", result.data.staff);
      commit("SET_PARAMS", result.data.parameter);
    },

    // action
    async getMemberInfo({ commit, state, getters }) {
      const { goodsItemGuid, memberCardId: cardId } = state;
      let member = null;
      let discount = null;
      let coupons = [];
      // 
      if (cardId && goodsItemGuid) {
        const result = await API.getMemberInfo({ cardId, goodsItemGuid });
        if (!result.success) return result;
        member = result.data.oilMemberInfo;
        discount = result.data.oilGoodsDiscountItem;
        coupons = result.data.coupons;
      }
      commit("SET_MEMBER_DATA", member);
      commit("SET_DISCOUNT_PARAMS", discount);
      commit("SET_COUPONS", coupons);
      // ,
      const map = autoPickShareDiscount(getters.shareDiscountKeys);
      commit("SET_DISCOUNT_CHECK_MAP", map);
      return { success: true };
    },

    // 
    async computeDiscount({ commit, state, getters }, { money, change }) {
      if (!state.goodsItemGuid) return;
      if (SET_DISCOUNT_CHECK_MAP_MANUAL) {
        SET_DISCOUNT_CHECK_MAP_MANUAL = false;
        return;
      }
      const params = {
        activity: getters.activity,
        discount: getters.discount,
        coupon: getters.coupon,
        point: getters.point,
        value: {
          value: getters.value,
          motherCard: getters.motherCard,
        },
        memberData: state.memberData,
        goodsItem: getters.currentGasItem,
      };
      const discountWay = {
        share: getters.shareDiscount.map((it) => it.name),
        conflict: getters.conflictDiscount.map((it) => it.name),
      };

      const { result, checkMap } = await CalculatorContext.calc(
        params,
        {
          price: money,
          checkMap: state.discountCheckMap,
          totalUnit: getters.totalUnit,
        },
        discountWay,
        change
      );
      if (checkMap) {
        // checkMapaction,
        SET_DISCOUNT_CHECK_MAP_MANUAL = true;
        commit("SET_DISCOUNT_CHECK_MAP", checkMap);
      }

      commit("SET_DISCOUNT_MAP", result);
    },

    async getOrderList({ commit }) {
      const result = await API.getOrderList();
      if (result.success) {
        commit(
          "SET_ORDER_LIST",
          result.data.reduce(
            (pre, item) => [
              ...pre,
              ...item.oilOrders.map((it) => ({
                ...it,
                oilGunGuid: item.oilGunGuid,
                key: it.oilOrderGuid,
              })),
            ],
            []
          )
        );
      }
      return result;
    },
  },
  getters: {
    // region 
    // 
    //  [] [] ,
    activity: (state, getters) => {
      const result = {
        label: "",
        name: "activity",
        list: [],
      };

      if (state.memberData) {
        result.list = state.memberData?.activityList ?? [];
      } else {
        const goodsItem = getters.currentGasItem;
        result.list = goodsItem?.activityList ?? [];
      }

      return result;
    },
    // 
    discount: (state) => {
      if (!state.memberData) return null;
      if (!state.discountParams) return null;
      return {
        name: "discount",
        discountMode: state.discountParams.discountMode, // 
        discountPerLiter: state.discountParams.discountPerLiter, // 
        memberDiscount: state.discountParams.discountRate, // 
        label: "",
      };
    },
    // 
    coupon: (state) => {
      if (!state.params) return null;
      if (!state.memberData) return null;
      const {
        couponConsumeMaxRate,
        isAllowMultiCouponForConsume: isAllowMultiCoupon,
      } = state.params.payParameter;
      return {
        isAllowMultiCoupon,
        couponConsumeMaxRate,
        list: state.coupons,
        pickedCoupons: state.pickedCoupons,
        name: "coupon",
        label: "",
      };
    },
    // 
    point: (state) => {
      if (!state.params) return null;
      if (!state.memberData) return null;
      const {
        pointPerYuan, // 1
        paidMoneyMaxRate, // 
        canPaidPointForConsumeValue, // ,
        canPaidPointForMemberPoint, // ,
      } = state.params.payParameter;
      return {
        memberAvailablePoint: state.memberData.availablePoint,
        pointPerYuan,
        paidMoneyMaxRate,
        canPaidPointForConsumeValue,
        canPaidPointForMemberPoint,
        label: "",
        name: "point",
      };
    },

    // 
    // .
    // 1. ; .
    // 2. , 
    // 3. , ,,,
    // 4. , 
    // ,

    // ,,,
    value: (state, getters) => {
      const { params, memberData } = state;
      if (!memberData) return null;
      const { currentSubOilCard } = getters;
      const { payParameter } = params;
      // ,
      if (currentSubOilCard) {
        return {
          ...currentSubOilCard,
          label: currentSubOilCard.goodsItemTypeName + "",
          everyUseValueMaxNumber: payParameter.everyUseValueMaxNumber,
          name: "value",
        };
      }
      return {
        oilDecreaseValue: memberData.oilDecreaseValue,
        availableValue: memberData.availableValue,
        everyUseValueMaxNumber: payParameter.everyUseValueMaxNumber,
        label: "",
        name: "value",
      };
    },

    // 
    motherCard: (state, getters) => {
      if (!state.memberData) return null;
      const { motherCardInfo } = state.memberData;

      // motherCardInfonull
      if (!motherCardInfo) return null;
      motherCardInfo.name = "motherCard";

      return {
        ...motherCardInfo,
        // ,
        oilDecreaseValue: motherCardInfo.motherAvailableValue > 0,
        name: "motherCard",
        label: "",
      };
    },

    // 
    currentSubOilCard: (state, getters) => {
      const { memberCardId, memberData } = state;
      if (!memberCardId || !memberData) return null;
      // valueMode1,
      if (memberData.valueMode === 1) return null;
      const pickedGun = getters.currentGasItem;
      if (!pickedGun) return null;
      return (
        memberData.oilCardInfoList.find(
          (it) => it.goodsItemTypeGuid === pickedGun.goodsItemTypeGuid
        ) || null
      );
    },
    // endregion

    // 
    currentGasItem: (state) => {
      return state.gunList.find((it) => it.id === state.oilIndexGuid);
    },

    // 
    totalUnit: (state, getters) => {
      if (!getters.currentGasItem) return 0;
      if (!state.orderMoney) return 0;
      const goods = getters.currentGasItem;
      const price = goods.isBargain ? goods.bargainPrice : goods.price;
      return Math.changeToDecimal(Math.divide(state.orderMoney, price));
    },

    /*    // 
    discountCheckMap: (state, getters) => {
      const map = {
        discount: false,
        activity: false,
        value: false,
        coupon: false,
        point: false,
        motherCard: false
      };
      let { shareDiscountKeys: share } = getters;

      // ,,
      if (share.includes("motherCard")) {
        share = share.reduce(
          (p, k) => (["value", "motherCard"].includes(k) ? p : [...p, k]),
          []
        );
      }

      // ,
      const list = [...share, ...state.currentConflict].filter(Boolean);
      list.forEach(key => (map[key] = true));
      return map;
    },*/

    // 
    shareDiscountKeys: (state, getters) => {
      const list = getters.paytypeMap ? getters.paytypeMap.shareDiscount : [];
      const keys = list.map((it) => it.name);
      return state.memberData ? keys : keys.filter((it) => it === "activity");
    },
    // 
    conflictDiscountKeys: (state, getters) => {
      const list = getters.paytypeMap
        ? getters.paytypeMap.conflictDiscount
        : [];
      const keys = list.map((it) => it.name);
      return state.memberData ? keys : keys.filter((it) => it === "activity");
    },

    // 
    restMoney: (state) => +(state.discountMap?.payCash ?? 0),

    // 
    /*
	 "type1"; 
	 "type2"; 
	 "type3"; 
	 "type4"; 
	 "type5"; 
	* */
    restMoneyFmtType: (state) =>
      state.params?.payParameter?.consumeMoneyIndexChoice ?? "type1",

    // 
    preferentialMoney: (state, getters) => {
      const { restMoneyFmtType: type, restMoney } = getters;
      return moneyFmt(type, restMoney).cut;
    },

    // 
    formatRestMoney: (state, getters) => {
      const { restMoneyFmtType: type, restMoney } = getters;
      return moneyFmt(type, restMoney).value;
    },

    isNeedLoopRequest: (state) => state.params?.isEnableDispenser ?? false,

    // 
    isNeedValidPassword: (state) => {
      const { params, discountMap } = state;
      if (!params) return false;
      const { payParameter } = params;
      const useValue = discountMap?.value?.money ?? 0;
      const pointValue = discountMap?.point?.point ?? 0;
      const couponValue = discountMap?.coupon?.money ?? 0;
      return (
        (payParameter.pointConsumeNeedPwd && pointValue > 0) ||
        payParameter.maxValueForNoSecret < useValue ||
        (payParameter.couponUsedNeedPwd && couponValue > 0)
      );
    },

    // 
    isNeedDelayRequest: (state, getters) => {
      const { value, motherCard } = getters;
      const { discountCheckMap: map } = state;
      // ,,
      if (!value) return false;
      // ,  ,
      return (
        (map["value"] && value.oilDecreaseValue) ||
        (map["motherCard"] && motherCard.oilDecreaseValue)
      );
    },

    // 
    enabledPayWay: (state, getters) => {
      const { thirdPaytype } = getters;
      const other = thirdPaytype.find((it) => it.name === "mobile");
      if (!other) return null;
      const map = {
        isWeChatPayEnabled: false,
        isAliPayEnabled: false,
        isBestPayEnabled: false,
        isUnionPayEnabled: false,
      };
      return other.includes.reduce(
        (p, i) => ({ ...p, [`is${i.key}Enabled`]: true }),
        map
      );
    },

    // 
    paytypeMap: (state, getters) => {
      if (!state.params) return null;
      return getUsedPaytype(
        state.params,
        getters.activity,
        getters.discount,
        getters.motherCard
      );
    },

    // 
    thirdPaytype: (state, getters) => {
      const { paytypeMap } = getters;
      let par = paytypeMap != null ? paytypeMap.thirdPaytype : [];
      return getThirdPaytype(par, state.params);
    },

    // 
    conflictDiscount: (state, getters) => {
      const { subCardVisible, conflictDiscountKeys: keys } = getters;
      if (!subCardVisible) {
        return key2list(
          getters,
          state,
          keys.filter((k) => k !== "value")
        );
      }

      return key2list(getters, state, keys);
    },
    // 
    shareDiscount: (state, getters) => {
      const { subCardVisible, shareDiscountKeys: keys } = getters;
      if (!subCardVisible) {
        return key2list(
          getters,
          state,
          keys.filter((k) => k !== "value")
        );
      }

      return key2list(getters, state, keys);
    },

    subCardVisible(state, getters) {
      const { memberData: member } = state;
      const { currentGasItem } = getters;

      if (!member || !currentGasItem) return false;

      // 
      const oilTypeMatch =
        member.goodsItemTypeGuid === currentGasItem.goodsItemTypeGuid;

      return oilTypeMatch || !state.memberData.isSlaveCard;
    },

    // 
    gasDecrease: (state, getters) => {
      const data = state.discountMap.value?.decreaseData ?? null;
      if (!data) return [];
      return data?.preferentialDescList ?? [];
    },
    gasValue: (state) => {
      const gasDecreaseList =
        state.discountMap.value?.decreaseData?.valueGasDecreaseList ?? [];
      const number = gasDecreaseList.reduce((p, x) => {
        if (x.activityAddValueGuid === "00000000-0000-0000-0000-000000000000")
          return p;
        return p + x.number;
      }, 0);
      return Math.fixed(number, 2);
    },
  },
};

// ,
function autoPickShareDiscount(share) {
  const map = {
    discount: false,
    activity: false,
    value: false,
    coupon: false,
    point: false,
    motherCard: false,
  };
  // let { shareDiscountKeys: share } = getters;

  // ,,
  if (share.includes("motherCard")) {
    share = share.filter((k) => !["value", "motherCard"].includes(k));
  }

  share.forEach((key) => (map[key] = true));

  return map;
}

const orderDiscountList = [
  "discount",
  "activity",
  "coupon",
  "point",
  "motherCard",
  "value",
];

function key2list(getters, state, keys) {
  // 0,map,
  const {
    discountMap: map,
    memberData,
    oilGunGuid,
    gunList,
    discountCheckMap: checkMap,
  } = state;
  const change = Math.changeToDecimal;
  if (!map.originMoney) return [];
  // 
  keys.sort(
    (a, b) => orderDiscountList.indexOf(a) - orderDiscountList.indexOf(b)
  );
  return keys.map((key) => {
    switch (key) {
      case "discount":
        let name = "";
        if (memberData) {
          name = `${memberData.memberGroupName}`;
        }
        return {
          label: name,
          name: "discount",
          description: map?.discount.label ?? "",
          value: map[key]?.money ?? "",
        };
      case "activity":
        return {
          label: "",
          name: "activity",
          description: map?.activity.label ?? "",
          value: map[key]?.money ?? "",
        };

      case "point":
        return {
          label: getters.point.label,
          name: getters.point.name,
          subValue: "",
          subLabel: checkMap.point
            ? `${map.point.point}`
            : map.point.totalPoint + "",
          value: map[key]?.money ?? "",
        };

      case "coupon":
        return {
          label: getters[key]?.label ?? "",
          name: getters[key].name,
          value: map[key]?.money ?? "",
          coupons: map.coupon?.coupons ?? [],
          subValue: "", //checkMap.coupon ? `-${map[key]?.count ?? 0}` : ``,
          subLabel: checkMap.coupon
            ? `${map[key]?.count ?? 0}`
            : `${map[key]?.enabledCount || ""}`,
          count: map[key]?.count ?? 0,
        };
      case "motherCard":
        return {
          label: "",
          name: "motherCard",
          subLabel: `${change(
            map?.value?.motherCard?.availableValue ?? 0
          )}`,
          value: map?.value.motherCard.checked ? map.value.money : 0 ?? 0,
        };
      case "value":
        return {
          label: getters.value.label,
          name: getters.value.name,
          subLabel: `${
            map[key]?.childCard?.availableValue.toFixed(2) ?? 0
          }`,
          value: map?.value.childCard.checked ? map.value.money : 0 ?? 0,
        };
      default:
        return {
          label: getters[key]?.label ?? "",
          value: map[key]?.money ?? "",
        };
    }
  });
}

/*
 "type1"; 
 "type2"; 
 "type3"; 
 "type4"; 
 "type5"; 
* */
function moneyFmt(type, money) {
  let value, cut;
  let times = 1;
  switch (type) {
    case "type1":
      money = Math.mult(money, times);
      value = Math.round(money);
      break;
    case "type2":
      times = 10;
      money = Math.mult(money, times);
      value = Math.round(money);
      break;
    case "type3":
      times = 100;
      money = Math.mult(money, times);
      value = Math.round(money);
      break;
    case "type4":
      money = Math.mult(money, times);
      value = Math.floor(money);
      break;
    case "type5":
      times = 10;
      money = Math.mult(money, times);
      value = Math.floor(money);
      break;
  }
  cut = Math.subtract(money, value);
  value = Math.divide(value, times);
  cut = Math.divide(cut, times);

  return {
    value,
    cut,
  };
}
